<template>
  <section id="app" style="height: 800px;">
    <div style="width:100%;">
      <Card style="width:80%;margin:0 auto;">
        <div slot="title">
          <div style="width: 50px; height: 50px; border-radius: 6px; cursor: pointer; border: 1px solid transparent; margin: 2px; filter: unset;display:inline-block">
            <img width=100% height=100% src="../picture/captain.jpg"/>
          </div>
          <div style="width: 200px;height: 50px;display:inline-block; font-size:16px">复仇者联盟V3.0排刀</div>
        </div>
        <Collapse v-model="collapseValue" style="text-align:left;">
          <Panel name="front">
            <span style="font-size:16px;line-height: 16px;">前卫</span>
            <div slot="content">
              <div class="ant-collapse-content ant-collapse-content-active">
                <div class="ant-collapse-content-box">
                  <template v-for="(item, i) in frontConfig">
                    <div :style="{backgroundPosition: item.position}" :class="item.className" :key="item.name" @click="selectCharacter(item, i, 0)">
                    </div>
                  </template>
                </div>
              </div>
            </div>
          </Panel>
          <Panel name="middle">
            <span style="font-size:16px;line-height: 16px;">中卫</span>
            <div slot="content">
              <div class="ant-collapse-content ant-collapse-content-active" style="">
                <div class="ant-collapse-content-box">
                  <template v-for="(item, i) in middleConfig">
                    <div :style="{backgroundPosition: item.position}" :class="item.className" :key="item.name" @click="selectCharacter(item, i, 1)">
                    </div>
                  </template>
                </div>
              </div>
            </div>
          </Panel>
          <Panel name="back">
            <span style="font-size:16px;line-height: 16px;">后卫</span>
            <div slot="content">
              <div class="ant-collapse-content ant-collapse-content-active" style="">
                <div class="ant-collapse-content-box">
                  <template v-for="(item, i) in backConfig">
                    <div :style="{backgroundPosition: item.position}" :class="item.className" :key="item.name" @click="selectCharacter(item, i, 2)">
                    </div>
                  </template>
                 </div>
              </div>
            </div>
          </Panel>
        </Collapse>
        <div>
          <p style="font-size:16px;text-align:left;">生成作业:</p>
          <div class="ant-collapse-content ant-collapse-content-active" style="text-align: left">
            <div class="ant-collapse-content-box">
              <div style="display: flex">
                <template v-for="item in workConfig">
                  <div :style="{backgroundPosition: item.position}" :class="item.className" :key="item.name">
                  </div>
                </template>
                <div class="workDiv">
                  <label>该刀可出数量:</label>
                  <Input v-model="newWrok.count" placeholder="" style="width: 60px" number/>
                </div>
                <div class="workDiv">
                  <label>对应boss:</label>
                  <Select v-model="newWrok.bossid" style="width:100px;margin-left: 2px">
                    <Option v-for="item in bossList" :value="item.key" :key="item.key">{{ item.value }}</Option>
                  </Select>
                </div>
                <div class="workDiv">
                  <label>预计伤害(w):</label>
                  <Input v-model="newWrok.avg_hp" placeholder="" style="width: 80px" number/>
                </div>
                <div class="workDiv">
                  <label>名称(示例.a0):</label>
                  <Input v-model="newWrok.id" placeholder="" style="width: 100px" />
                </div>
                <div class="workDiv">
                  <label>类型:</label>
                  <Select v-model="newWrok.group_type" style="width:100px;margin-left: 2px">
                    <Option v-for="item in groupList" :value="item.key" :key="item.key">{{ item.value }}</Option>
                  </Select>
                </div>
                <div class="workDiv">
                  <label>权重:</label>
                  <Select v-model="newWrok.weight" style="width:80px;margin-left: 2px">
                    <Option v-for="item in weightList" :value="item.key" :key="item.key">{{ item.value }}</Option>
                  </Select>
                </div>
                <div class="workDiv">
                  <Button type="primary" @click="createNewWork">生成作业</Button>
                </div>
              </div>
              <p></p>
              <Select v-model="rates.one" size="small" style="width:50px;margin-left: 2px">
                <Option v-for="item in rateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
              <Select v-model="rates.two" size="small" style="width:50px;margin-left: 4px">
                <Option v-for="item in rateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
              <Select v-model="rates.three" size="small" style="width:50px;margin-left: 4px">
                <Option v-for="item in rateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
              <Select v-model="rates.four" size="small" style="width:50px;margin-left: 4px">
                <Option v-for="item in rateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
              <Select v-model="rates.five" size="small" style="width:50px;margin-left: 4px">
                <Option v-for="item in rateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </div>
          </div>
        </div>
        <div>
          <p style="font-size:16px;text-align:left;">已有作业:</p>
          <Table ref="workTable" :columns="gridConfig.gridColumns"
                 :data="gridConfig.gridData" stripe border>
            <template slot-scope="{ row, index }" slot="action">
              <Button type="error" size="small" @click="removeWork(index)">删除</Button>
            </template>
          </Table>
          <br>
          <div style="text-align: left">
           <Button type="primary" size="large" @click="exportExcel"><Icon type="ios-download-outline"></Icon>导出作业数据</Button>
           <Button type="primary" size="large" @click="showModal" style="margin-left: 10px;"><Icon type="ios-download-outline"></Icon>查看作业Json数据</Button>
          </div>
        </div>
      </Card>
    </div>
    <Modal
            v-model="modalStatus"
            title="作业Json数据"
            @on-ok="closeModal">
      <div>
        {{gridConfig.gridData}}
      </div>
    </Modal>
  </section>
</template>

<script>
import { columns } from '@/config/backImg'
import { workJson } from '@/config/lineupJson'
import { getValueByKey, getListValueByKey } from "./functions"
import { codeValueList} from "./constant/basic"

export default {
  name: 'Home',
  components: {},
  mixins: [ columns, workJson ],
  data() {
    return {
        showPage: true,
        collapseValue: 'front',
        modalStatus: false,
        gridConfig: {
            gridColumns: [
                {
                    title: '标识',
                    minWidth: 120,
                    align: 'center',
                    key: 'id'
                },
                {
                    title: '阵容',
                    minWidth: 120,
                    align: 'center',
                    key: 'lineup',
                    render: (h, params) => {
                        return h('div', [
                            h('div', {
                                class: ['characterSelectDiv'],
                                style: {
                                    backgroundPosition: getValueByKey(this.frontConfig, this.middleConfig, this.backConfig, params.row.lineup.one)
                                }
                            }),
                            h('div', {
                                class: ['characterSelectDiv'],
                                style: {
                                    backgroundPosition: getValueByKey(this.frontConfig, this.middleConfig, this.backConfig, params.row.lineup.two)
                                }
                            }),
                            h('div', {
                                class: ['characterSelectDiv'],
                                style: {
                                    backgroundPosition: getValueByKey(this.frontConfig, this.middleConfig, this.backConfig, params.row.lineup.three)
                                }
                            }),
                            h('div', {
                                class: ['characterSelectDiv'],
                                style: {
                                    backgroundPosition: getValueByKey(this.frontConfig, this.middleConfig, this.backConfig, params.row.lineup.four)
                                }
                            }),
                            h('div', {
                                class: ['characterSelectDiv'],
                                style: {
                                    backgroundPosition: getValueByKey(this.frontConfig, this.middleConfig, this.backConfig, params.row.lineup.five)
                                }
                            })
                        ]);
                    }
                },
                {
                    title: '描述',
                    minWidth: 120,
                    align: 'center',
                    key: 'desc'
                },
                {
                    title: '对应boss',
                    minWidth: 120,
                    align: 'center',
                    key: 'bossid',
                    render: (h, params) => {
                        return h('span', getListValueByKey(codeValueList.bossStatus, params.row.bossid))
                    }
                },
                {
                    title: '操作',
                    slot: 'action',
                    width: 150,
                    align: 'center'
                }
            ],
            gridData: workJson
        },
        workConfig: [
            { name: 'unknown1', position: '', cid: 0, className: 'battle_search_empty' },
            { name: 'unknown2', position: '', cid: 0, className: 'battle_search_empty' },
            { name: 'unknown3', position: '', cid: 0, className: 'battle_search_empty' },
            { name: 'unknown4', position: '', cid: 0, className: 'battle_search_empty' },
            { name: 'unknown5', position: '', cid: 0, className: 'battle_search_empty' }
        ],
        rates: {
            one: 5,
            two: 5,
            three: 5,
            four: 5,
            five: 5,
        },
        rateList: [
            { label: '1', value: 1},
            { label: '2', value: 2},
            { label: '3', value: 3},
            { label: '4', value: 4},
            { label: '5', value: 5}
        ],
        newWrok: {
            count: 0,
            bossid: '',
            id: '',
            avg_hp: '',
            group_type: '',
            weight: 0,
            desc: '',
            lineup: {}
        },
        weightList: [ {key: 1, value: '优先'}, {key: 0, value: '普通'} ],
        groupList: [ {key: 'shenyue', value: '深月刀'}, {key: 'heiqi', value: '黑骑刀'}, {key: 'didi', value: '弟弟刀/法刀'} ],
        bossList: [
            {key: 'A1', value: '1周目飞龙'},
            {key: 'A2', value: '1周目狮鹫'},
            {key: 'A3', value: '1周目黑猪'},
            {key: 'A4', value: '1周目独角兽'},
            {key: 'A5', value: '1周目螃蟹'},
            {key: 'A6', value: '1周目狂暴螃蟹'},
            {key: 'B1', value: '2周目飞龙'},
            {key: 'B2', value: '2周目狮鹫'},
            {key: 'B3', value: '2周目黑猪'},
            {key: 'B4', value: '2周目独角兽'},
            {key: 'B5', value: '2周目螃蟹'},
            {key: 'B6', value: '2周目狂暴螃蟹'}
        ]
    }
  },
  methods: {
      selectCharacter(character, index, configType) {
          if ('characterDiv' === character.className) {
              let existConfig = this.workConfig.filter(t => t.name.indexOf('unknown') > -1)
              if (existConfig && existConfig.length > 0) {
                  this.changeSelectedClass(configType, index)
                  this.updateCharacter(character)
              }
          } else {
              this.changeSelectedClass(configType, index)
              let existConfig = this.workConfig.filter(t => t.cid == character.cid)
              this.workConfig = this.workConfig.filter(t => t.cid != character.cid)
              if (existConfig && existConfig.length > 0) {
                  let time = new Date()
                  const unknownCharacter = { name: 'unknown' + time.getTime(), position: '', cid: 0, className: 'battle_search_empty' }
                  this.workConfig.push(unknownCharacter)
              }
          }
      },
      changeSelectedClass(configType, index) {
          if (0 == configType) {
              this.frontConfig[index].className = (this.frontConfig[index].className === 'characterDiv' ? 'characterSelectDiv' : 'characterDiv')
          } else if (1 == configType) {
              this.middleConfig[index].className = (this.middleConfig[index].className === 'characterDiv' ? 'characterSelectDiv' : 'characterDiv')
          } else {
              this.backConfig[index].className = (this.backConfig[index].className === 'characterDiv' ? 'characterSelectDiv' : 'characterDiv')
          }
      },
      removeWork(index) {
          this.gridConfig.gridData.splice(index, 1)
      },
      updateCharacter(character) {
          let index = -1;
          for (let i = 0;i < this.workConfig.length;i ++) {
              if (this.workConfig[i].name.indexOf('unknown') > -1) {
                  index = i
              }
          }
          if (index >= 0) {
              this.workConfig.splice(index, 1)
              character.className = 'characterSelectDiv'
              this.workConfig.push(character)
          }
      },
      createNewWork() {
          let desc = ''
          let line = {}
          for (let i = 0;i < this.workConfig.length;i ++) {
              let item = this.workConfig[i]
              desc += item.name
              if (i === 0) {
                  line.one = item.cid
                  desc += this.rates.one
              } else if (i === 1) {
                  line.two = item.cid
                  desc += this.rates.two
              } else if (i === 2) {
                  line.three = item.cid
                  desc += this.rates.three
              } else if (i === 3) {
                  line.four = item.cid
                  desc += this.rates.four
              } else if (i === 4) {
                  line.five = item.cid
                  desc += this.rates.five
              }
          }
          this.newWrok.desc = desc
          this.newWrok.lineup = line
          const work = this.newWrok
          this.gridConfig.gridData.push(work)
      },
      exportExcel() {
          this.$refs.workTable.exportCsv({
              filename: '作业配置数据'
          })
      },
      showModal() {
          this.modalStatus = true
      },
      closeModal () {
          this.modalStatus = false
      }
  }
}
</script>
<style>
  .characterDiv {
    width: 50px;
    height: 50px;
    background-size: 566.667px;
    border-radius: 6px;
    opacity: 0.6;
    cursor: pointer;
    border: 1px solid transparent;
    display: inline-block;
    margin: 2px;
    filter: unset;
    background-image: url("../picture/charas.png");
  }
  .characterSelectDiv {
    width: 50px;
    height: 50px;
    background-size: 566.667px;
    border-radius: 6px;
    cursor: pointer;
    border: 1px solid transparent;
    display: inline-block;
    margin: 2px;
    filter: unset;
    background-image: url("../picture/charas.png");
  }
  .battle_search_empty {
    width: 50px;
    height: 50px;
    background-size: 566.667px;
    border-radius: 6px;
    background-image: url("../picture/unknown.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    display: inline-block;
    margin: 2px;
  }
  .workDiv {
    margin-left: 8px;
    margin-top:12px;
  }
</style>

