export const getValueByKey = (list1, list2, list3, key) => {
    let result = ''
    let item1 = list1.find(p => p.cid == key)
    if (item1) {
      result = item1.position
    }
    if (result) {
      return result
    } else {
        let item2 = list2.find(p => p.cid == key)
        if (item2) {
            result = item2.position
        }
        if (result) {
          return result
        } else {
            let item3 = list3.find(p => p.cid == key)
            return item3.position
        }
    }
}

export const getListValueByKey = (list, key) => {
    if (list) {
        let item = list.find(p => p.key == key)
        if(!item){
            return key
        }
        return item.value
    }
    return key
}
